@import "../../css/flex";
@import "../../css/colors";
.header {
    @extend %flex;
    padding-block: 30px;
    background-color: $deep-black;
    color: white;
    position: absolute;
    width: 100%;
}
.header-links {
    @extend %flex;
    gap: 10px;
}

.link {
    text-decoration: none;
    color: white;
    cursor: pointer;
}

.header-title {
    font-weight: bold;
    letter-spacing: 2px;
}
